module.exports = "\n@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);\n/* 'Open Sans' font from Google Fonts */\n.login {\n  width: 400px;\n  margin: 16px auto;\n  font-size: 16px;\n}\n.login-header,\n.login p {\n  margin-top: 0;\n  margin-bottom: 0;\n}\n.login-triangle {\n  width: 0;\n  margin-right: auto;\n  margin-left: auto;\n  border: 12px solid transparent;\n  border-bottom-color: rgb(15, 66, 107);\n}\n.login-header {\n  background: rgb(12, 77, 129);\n  padding: 20px;\n  font-size: 1.4em;\n  font-weight: normal;\n  text-align: center;\n  text-transform: uppercase;\n  color: #fff;\n}\n.login-container {\n  background: #ebebeb;\n  padding: 12px;\n}\n.login p {\n  padding: 12px;\n}\n.login input {\n  box-sizing: border-box;\n  display: block;\n  width: 100%;\n  border-width: 1px;\n  border-style: solid;\n  padding: 16px;\n  outline: 0;\n  font-family: inherit;\n  font-size: 0.95em;\n}\n.login input[type=\"email\"],\n.login input[type=\"password\"] {\n  background: #fff;\n  border-color: #bbb;\n  color: #555;\n}\n.login input[type=\"email\"]:focus,\n.login input[type=\"password\"]:focus {\n  border-color: #888;\n}\n.login input[type=\"submit\"] {\n  background: rgb(1, 29, 51);\n  border-color: transparent;\n  color: #fff;\n  cursor: pointer;\n}\n.login input[type=\"submit\"]:hover {\n  background: #17c;\n}\n.login input[type=\"submit\"]:focus {\n  border-color: #05a;\n}\n.has-error input[type=\"email\"],.has-error input[type=\"password\"] {\n    border-color: rgb(216, 12, 12);\n    color: rgb(230, 14, 14);\n}\n/*# sourceMappingURL=data:application/json;base64, */"